@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #f2f2f2;
}

body {
  background-color: #212529;
  text-align: center;
}

button {
  background-color: #f1356d;
  color: #f2f2f2;
  font-size: 16px;
  font-weight: bold;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.3s ease;
}

.header {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.header h1 {
  color: #f1356d;
}
.header .links {
  margin-left: auto;
}
.header a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.header a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}